html{
  height: 100%;
}

body {
  width: 100%;
  min-height:100%;
  background:
    url("./images/backgroundimage2.jpg");
    background-size: cover;



}

* {
  height: 100%;
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
  margin: 0;
  padding: 0;
  border: 0;
}

.movieWrapper {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 auto;
      flex: 1 1 auto;
  -ms-flex-direction: column;
      flex-direction: column;
  height: 100%;
}

.movieHeader {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 5%;
      flex: 1 1 5%;
  min-height: 2em;
  max-height: 2em;
  font-size: 3.2em;
  color: #40E0D0;
  font-family: Impact;
  text-align: center;
  -ms-flex-line-pack: center;
      align-content: center;
  padding:10px 0px 10px 0px;
  margin-bottom: 6px;
  background-color: rgba(0,0,0,0.5);
  text-shadow: 3px 3px 10px #000;
  display: block;
}

.movieBody {
  padding-top: 24px;
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 85%;
      flex: 1 1 85%;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  -ms-flex-align: start;
      align-items: flex-start;
  -ms-flex-line-pack: start;
      align-content: flex-start;
  overflow: auto;
}

.movieFooter {
  display: -ms-flexbox;
  display: flex;
  -ms-flex: 1 1 5%;
      flex: 1 1 5%;
  background-color: rgba(0,0,0,0.5);
}

.googleButton{
  background: url("./images/googleButton.png");
  background-repeat: no-repeat;
  height: 65px;
  width: 300px;
  text-align: center;
  position: absolute;
  top:200px;
  left:0;
  right:0;
  margin: auto;
}

.googleButton:hover{
  background: url("./images/googleButtonHover.png") no-repeat;
  height: 65px;
  width: 300px;

}


.signOut{
  height: 25px;
  width: 75px;
  background-color: #4169E1;
  position: absolute;
  top: 0px;
  right: 5px;
}
.signOut:hover{
  background-color: #87CEEB;
}
.signOut:active{
  background-color: #708090;
}

.facebookButton{
  background: url("./images/facebookButton.png") no-repeat;
  background-repeat: no-repeat;
  height: 65px;
  width: 300px;
  position: absolute;
  top:260px;
  left:0;
  right:0;
  margin: auto;
}

.facebookButton:hover{
  background: url("./images/facebookButtonHover.png")no-repeat;
  height: 65px;
  width: 300px;
}

.twitterButton{
  background: url("./images/twitterButton.png") no-repeat;
  height: 65px;
  width: 300px;
  position: absolute;
  top:320px;
  left:0;
  right:0;
  margin: auto;
}
.twitterButton:hover{
  background: url("./images/twitterButtonHover.png")no-repeat;
  height: 65px;
  width: 300px;
}



